<template>
 <div class="project-detail">
     <section class="hero-title">
       <div class="wrapper">
        <img class="coding" src="../assets/img/coding.svg" alt=""/>
         <h1> {{ detailItem[0].title }} </h1>
         <p>{{ detailItem[0].description }}</p>
         <a class="visit-website"
          v-if="detailItem[0].visit_website"
          :href="detailItem[0].visit_website_url"
          target="_blank">
          Visit Website</a>
       </div>
       <div class="wrapper">
            <img v-for="image in detailItem[0].full_images" :src="image" alt="" class="work-image">
            <div class="links">
                <router-link class="visit-website prevButton" :to="getPrevItemsUrl">Previos Project</router-link>
                <router-link class="visit-website" :to="getNextItemsUrl">Next Project</router-link>
            </div>
       </div>
     </section>
 </div>
</template>

<script>
import Works from './Works.vue'
import data from '@/assets/data/data.json'
export default {
    name: 'Project',
    data(){
        return{
            detailItem: data.works.filter((el,index) => {
                return el.url == this.$route.params.url
            })
        }
    },
    computed: {
        getNextItemsUrl(){
            return data.works[parseInt(data.works.findIndex(el => el.url == this.$route.params.url) + 1)].url ? data.works[parseInt(data.works.findIndex(el => el.url == this.$route.params.url) + 1)].url : '/'
        },
        getPrevItemsUrl(){
            return data.works[parseInt(data.works.findIndex(el => el.url == this.$route.params.url) - 1)].url ? data.works[parseInt(data.works.findIndex(el => el.url == this.$route.params.url) - 1)].url : '/'
        }
    }
}
</script>

<style scoped>   
.links{
    display:flex;
    width:100%;
    margin: 80px 0 -20px 0;
    justify-content:space-between
}
.links a{
    line-height:20px
}
.hero-title h1{
    margin:15px 0
}
.work-image{margin-top: 30px;width: 100%!important;}
.spacer{margin-top:2em;display:block;width:100%}
.prevButton{
    background:url(data:image/svg+xml;base64,) left no-repeat;
    background-size:15px;
    padding-right:0;
    padding-left:25px;}
</style>    


